.rt-uno-over-gallery {
    position: relative;
    width: 100%;
    z-index: 1;

    .top-curve {
        height: 30px;
        width: 100%;
        position: absolute;
        top: -1px;
        z-index: 2;

        svg {
            display: block;
            height: 30px;
            width: 100%;
            path {
                display: block;
                height: 30px;
                width: 100%;
                fill: $white;
            }
        }
    }

    a.gallery-image {
        display: block;
        width: 25%;
        height: 420px;
        float: left;
        overflow: hidden;

        img {
            transition: map-get( $trans, 350 );
            height: 100%;
            width: 100%;
            object-fit: cover;
        }

        &:hover {
            img {
                transform: scale(1.05);
            }
        }
    }

    .bottom-curve {
        height: 30px;
        width: 100%;
        position: absolute;
        bottom: -1px;
        z-index: 2;

        svg {
            display: block;
            height: 30px;
            width: 100%;
        }
            
        .bottom-path {
            display: block;
            height: 30px;
            width: 100%;
            fill: $main-color !important;
        }
    }
}




/*---------------------------------------------------------
-- Medium (md) Screen Size Media Queries - less than 992px
---------------------------------------------------------*/
@include media-breakpoint-down(md) {
	.rt-uno-over-gallery {
        a.gallery-image {
            width: 50%;
        }
    }
}


/*---------------------------------------------------------
-- Small (sm) Screen Size Media Queries - less than 768px
---------------------------------------------------------*/
@include media-breakpoint-down(sm) {
	.rt-uno-over-gallery {
        a.gallery-image {
            height: 320px;
        }
    }
}

/*---------------------------------------------------------
-- Extra Small (xs) Screen Size Media Queries - less than 576px
---------------------------------------------------------*/
@include media-breakpoint-down(xs) {
	.rt-uno-over-gallery {
        a.gallery-image {
            width: 100%;
            float: none;
        }
    }
}